<template>
  <div>
    <intro></intro>

    <div id="portfolio-container">
        <category-item v-for="category in categories" :key="category.title" :categorydata="category"></category-item>
    </div>

    <about></about>
    
    <side-nav></side-nav>

    <div id="footer">
      <!-- might need to become normal links to a pdf and a mailto -->
      <a href="https://drive.google.com/open?id=19IHBiW7rXlfk5GWZn1Np9cJfJtT2kEpB" class="right-link">Download CV</a>
      <a href="mailto:martina.bonetti1@gmail.com" class="left-link">Get in touch</a>
    </div>
  </div>
</template>

<script>
/*
index page (i.e. landing page of the website)

This is where we list a bunch of portfolio items, show the intro page, etc.

Should reference some global data about the whole portfolio
*/

import CategoryItem from './CategoryItem.vue'
import SideNav from './SideNav.vue'
import Intro from './Intro.vue'
import About from './About.vue'
import { mapState } from 'vuex'
import {store} from '../store'
import scroll from 'scroll'
import page from 'scroll-doc'

export default {
  name: 'index',
  props: ['itemid'],
  data () {
    return {
      indexScroll: 0,
      scrollHandler: ()=>{
        store.commit('indexScroll', store.state.scrollPos);
      }
    };
  },
  computed: mapState({
    newScrollPos(state){
      console.log(state.scrollPos);
      return state.scrollPos;
    },

    categories(state){
      return state.categories;
    }
  }),
  updated () {
    this.indexScroll = state.scrollPos;
    console.log(state.scrollPos);
  },
  components: {
    CategoryItem,
    SideNav,
    Intro,
    About
  },
  mounted () {
    console.log(store.state.indexScroll);
    setTimeout(()=> {
      if(this.itemid == 'about' && store.state.indexScroll == "about") {
        window.scrollTo(0,document.getElementById('about').offsetTop);
      } else {
        window.scrollTo(0,store.state.indexScroll);
      }
      window.addEventListener('scroll', this.scrollHandler);
    }, 0);//to render the portfolioitems properly.
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollHandler);
  }
}
</script>

<style scoped>
  #portfolio-container{
    width: 100%;
    overflow: hidden;
  }

  #portfolio-container::before{
    content: " ";
    display: block;
    position: fixed;
    top: 112px;
    left: 50%;
    margin-left: calc(35vw - 50px);
    width: 120px;
    height: 120px;
    border-right: 5.5px solid #E3897D;
    border-top: 4px solid #E3897D;
  }

  #footer{
    position: fixed;
    bottom: 0;
    width: 70vw;
    left: 50%;
    margin-left: -35vw;
    padding-bottom: 50px;
    z-index: 5;
  }

  @media (max-width: 800px){
    #footer{
      width: 100%;
      left: 0;
      margin-left: 0;
      padding: 0 40px;
      bottom: 30px;
    }
  }

</style>



