<template>
  <div id="category-page">

    <project :portfolio-item="category.mainItem" :category-title="category.title" :category-number="category.number"></project>
    <div class="more-projects-line">
      <p>{{category.mainItem.moreProjectsLine}}</p>
      <img src="../assets/img/arrow-down.png">
    </div>
    <project v-for="item in category.relatedItems" :portfolio-item="item" :category-title="category.title" :category-number="category.number"></project>


    <a class="back-link" href="javascript:history.back()"> <img src="../assets/img/arrow-left.png"> Go back</a>

    <div class="category-navigation">
      <span>projects</span>
      <a @click="() => {scrollToElement(category.mainItem.id)}">{{category.mainItem.title}}</a>
      <a v-for="item in category.relatedItems" @click="() => {scrollToElement(item.id)}">{{item.title}}</a>
    </div>
  </div>
</template>

<script>

import { mapState } from 'vuex'
import {store} from '../store'
import Project from './Project.vue'

import scroll from 'scroll'
import page from 'scroll-doc'

export default {
  name: 'category-page',
  props: ['categoryId', 'projectId'],
  components: {Project},
  data () {
    return {
      scrollHandler: () => {
        console.log("scroll");
      }
    };
  },
  methods: {
    scrollToElement(id){
      console.log(document.getElementById(id).offsetTop);
      scroll.top(page(), document.getElementById(id).offsetTop, {duration: 300}, (error, scrollTop) => {console.log(scrollTop)});
    }
  },
  computed: mapState({
    category(state){
      for(let category of state.categories){
        if(category.id == this.categoryId){
          return category;
        }
      }
    }
  }),
  mounted() {
    store.commit('mount');
    if(this.projectId){
      setTimeout(() => {
        scroll.top(page(), document.getElementById(this.projectId).offsetTop, {duration: 300}, (error, scrollTop) => {console.log(scrollTop)})
      }, 200);
    } else{
      scroll.top(page(), 0);
    }

    window.addEventListener('scroll', this.scrollHandler);
  },
  destroyed() {
    window.removeEventListener('scroll', this.scrollHandler);
  }
}
</script>

<style scoped lang="scss">

$primary-color: #B72940;
$secondary-color: #C86071;

#category-page{
  width: 100%;
  max-width: 1000px;
  margin: auto;
  position: relative;
}

.back-link{
  transform: rotate(90deg);
  transform-origin: bottom left;
  text-transform: uppercase;
  font-size: 12px;
  position: relative;
  font-weight: bold;

  position: fixed;
  top: 95px;
  left: 50%;
  margin-left: calc(-35vw - 100px);
  text-decoration: none;
}

.back-link img{
  width: 15px;
}

a{
  cursor: pointer;
}

.category-navigation{
  position: fixed;
  top: 95px;
  width: 50vw;
  left: 50%;
  margin-left: calc(35vw + 85px);
  transform: rotate(90deg);
  transform-origin: top left;

  span, a{
    margin-right: 30px;
    font-size: 12px;
    font-weight: bold;
    text-decoration: none;
  }
}

.more-projects-line{
  width: 100%;
  text-align: center;
  padding-bottom: 100px;

  p {
    text-decoration: underline;
    font-size: 12px;
    margin-bottom: 5px;
  }

  img{
    height: 12px;
  }
}

@media (max-width: 800px){
  .category-container{
    left: 40px;
    width: 100%;
  }

  #category-page{
    padding-top: 75px;
  }

  .featured-image{
    position: relative;
    top: 0;
    width: 80%;
    margin: 10px 10% -30px 10%;

  }

  .project-info-container{
    width: 100%;
    padding:  40px;

    .project-name-1{
      margin-left: 0;
    }

    .project-name-2{
      margin-left: 100px;
    }
  }

  .project-icon, .project-info{
    display: inline;
  }

  .project-icon{
    position: relative;
    top: 3px;
    margin-right: 20px;
  }

  .project-description{
    width: calc(100vw - 100px);
    margin: 0 40px 40px 60px;
    padding-top: 20px;
  }

  .back-link{
    left: 15px;
    margin-left: 0px;
  }

  .category-navigation{
    left: auto;
    margin-left: 0;
    right: 10px;
    width: 80vh;
    display: none;
  }
}
</style>



