<template>
  <div class="project-container" :id="portfolioItem.id">
    <div class="category-container">
      <h2 class="number" :style="textStrokeObject">{{categoryNumber}}</h2>
      <h1 class="category-name" :style="colorObject">{{categoryTitle}}</h1>
    </div>

    <img class="featured-image" :src="featuredImage">

    <div class="project-info-container">

      <img class="project-icon" src="../assets/img/icons/public-services.png">
      <h3 class="project-info">{{portfolioItem.descriptor}}</h3>

      <h1 class="project-name-1" :style="colorObject">{{portfolioItem.titleSegment1}}</h1>
      <h1 class="project-name-2" :style="colorObject">{{portfolioItem.titleSegment2}}</h1>

      <h2 class="project-context">{{portfolioItem.context}}</h2>
      <h2 class="project-type">{{portfolioItem.type}}</h2>

    </div>

    <div class="project-description" :style="descriptionStyleObject">
      <p>
        {{portfolioItem.description}}
      </p>
    </div>


    <!-- end of top -->
    <div class="details-container">

    <template v-for="(additionalContentItem, index) in portfolioItem.additionalContent">


      <template v-if="(index%2)===0 && index > 0">
        <div class="clearboth"></div>
      </template>

      <template v-if="additionalContentItem.type == 'image'">
        <img class="content-image" :src="additionalPictures[additionalContentItem.data]">
      </template>

      <template v-if="additionalContentItem.type == 'vimeo'">
        <vimeo-player :video-id='additionalContentItem.data' :player-width='playerWidth'></vimeo-player>
      </template>

      <template v-if="additionalContentItem.type == 'text'">
        <div class="content" v-html="additionalContentItem.data">
          
        </div>
      </template>

    </template>
    
    </div>

  </div>
</template>


<script>

import { mapState } from 'vuex'
import {store} from '../store'
import { vueVimeoPlayer } from 'vue-vimeo-player'

export default {
  name: 'project',
  props: ['portfolioItem', 'categoryTitle', 'categoryNumber'],
  components: { 'vimeo-player': vueVimeoPlayer },
  data () {
    return {
      playerWidth: window.innerWidth > 800 ? 500 : 330
    };
  },
  methods: {
    elementWidth(){
      return this.$el.offsetWidth;
    }
  },
  computed: mapState({
    featuredImage(){
      return require('../assets/img/' + this.portfolioItem.featuredImage);
    },
    colorObject(){
      return {color: this.portfolioItem.textColor};
    },
    descriptionStyleObject(){
      return {
        backgroundColor: this.portfolioItem.descriptionBackgroundColor,
        maxWidth: this.portfolioItem.descriptionWidth + "px"
      }
    },
    textStrokeObject(){
      return {textStroke: '2px ' + this.portfolioItem.textColor, 'webkit-text-stroke': '2px ' + this.portfolioItem.textColor};
    },
    activeItem(state){
      let relPos = state.scrollPos-this.offsetTop;
      if(relPos > -window.innerHeight/2 && relPos < -window.innerHeight/2){
        store.commit('activeItem', this.portfolioItem.id);
      }
      return state.activeItem;
    },
    additionalPictures(){
      let additionalPictures = {};
      for(let contentItem of this.portfolioItem.additionalContent){
        if(contentItem.type == 'image'){
          additionalPictures[contentItem.data] = require('../assets/img/' + contentItem.data);
        }
      }
      return additionalPictures;
    }
  }),
  updated(){
    this.offsetTop = this.$el.offsetTop;
    this.playerWidth = this.elementWidth();
  },
  mounted() {
    store.commit('mount');
    window.scrollTo(0,0);//scroll to top after loading.
    this.playerWidth = this.elementWidth();
    console.log(this.playerWidth)
  }
}
</script>


<style scoped lang="scss">
$primary-color: #B72940;
$secondary-color: #C86071;

.project-container{
  position: relative;
  padding-bottom: 100px;
}

.featured-image{
  position: absolute;
  height: 555px;
  right: 0;
  top: 90px;
}

.project-icon{
  width: 18px;
}

.category-container{
  position: relative;
  left: 40%;
  padding-top: 20px;
  z-index: 2;
  max-width: 60%;

  .number, .category-name{
    font-size: 3.6em;
    margin: 0;
    text-transform: uppercase;
  }

  .number{
    color: white;
    text-stroke: 2px $primary-color;
    -webkit-text-stroke: 2px $primary-color;
  }
}

.project-info-container{
  width: 40%;

  .project-info{
    text-transform: uppercase;
    font-weight: normal;
    font-size: 16px;
  }

  .project-name-1, .project-name-2{
    margin: 0;
    color: $primary-color;
    font-size: 3.6em;
  }

  .project-name-1{
    margin: 0 0 -10px -100px;
  }

  .project-context{
    padding-top: 20px;
  }

  .project-context, .project-type{
    font-weight: normal;
    font-size: 32px;
    margin: 0;
  }

  .project-type{
    text-decoration: underline;
  }

}

.project-description{
  position: relative;
  z-index: 2;
  background-color: $secondary-color;
  padding: 5px 10px;
  margin: 40px 0 120px -70px;
  width: 90%;

  p{
    margin: 0 0 15px -30px;
    color: #000;
  }
}


.details-container{
  width: calc(100% + 0px);
  margin-left: 0px;
  clear: both;

  .content-image{
    width: 45%;
  }

  .content{
    width: 475px;
    display: flex;
    flex-direction: column;
    justify-content: center;

  }

  .content-image, .content, div{
    float: left;
  }

  > *:nth-child(2){
    margin-left: 25px;
  }

  .content{
    padding-right: 25px;
  }

}

.details-container::after{
  display: block;
  content: "";
  clear: both;
}

.clearboth{
  clear: both;
  width: 100%;
}

@media (max-width: 800px){
  .category-container{
    left: 40px;
  }

  .featured-image{
    position: relative;
    top: 0;
    width: 80%;
    height: auto;
    margin: 10px 10% -30px 10%;

  }

  .project-info-container{
    width: 100%;
    padding:  40px;

    .project-name-1{
      margin-left: 0;
    }

    .project-name-2{
      margin-left: 0px;
    }
  }

  .project-icon, .project-info{
    display: inline;
  }

  .project-icon{
    position: relative;
    top: 3px;
    margin-right: 20px;
  }

  .project-description{
    width: calc(100vw - 100px);
    margin: 0 40px 40px 60px;
    padding-top: 20px;
  }

  .content-row{
    width: calc(100% - 80px);
    margin: 0 40px;

    .content, .content-image{
      width: 100%;
    }

    > *:nth-child(2){
      margin-left: 0;
    }
  }

  .details-container{
    padding: 40px;

    .content-image{
      width: 100%;
    }

    .content{
      width: 100%;
      margin: 0;
    }
  }

}
</style>



