<template>
	<div id="app-container">

		<div id="header">
			<router-link to="/"><h1>Martina Bonetti</h1></router-link>
			<a href="#about" @click="setIndexScrollToAbout" class="right-link">About</a>
		</div>
			<router-view></router-view>
	</div>
</template>

<script>
	import {store} from '../store'


	export default {
		name: 'app',
		data () {
			return {
				scrollUpdate: function(){
					store.commit('scrollPos');
				},
				setIndexScrollToAbout(){
					store.commit('indexScroll', 'about')
				}
			};
		},
		mounted (){
			window.addEventListener('scroll', this.scrollUpdate);
		},
		destroyed(){
			window.removeEventListener('scroll', this.scrollUpdate);
		}
	}
</script>

<style scoped>

	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
	  opacity: 0
	}
	#header{
		width: 70vw;
		margin: auto;
		height: 100px;
		padding: 25px 0 0 0;
		position: fixed;
		top: 0;
		left: 15vw;
		z-index: 5;
	}

	#header h1{
		display: inline-block;
		padding: 10px 0;
		margin: 10px 0;
		position: absolute;
		font-size: 26px;
		text-shadow: 0px 2px 0px #fff, 
		2px 0px 0px #fff, 
		0px -2px 0px #fff, 
		-2px 0px 0px #fff,
		2px 2px #fff,
		-2px 2px #fff,
		-2px -2px #fff,
		2px -2px #fff;
	}

	#header a{
		margin-top: 30px;
	}

	@media (max-width: 800px){
		#header{
			width: 100%;
			left: 0;
			margin-left: 0;
			padding: 10px 40px;
		}
	}

</style>

<style>
	@font-face {
	  font-family: 'SimpleKölnBonn';
	  src: url('../assets/fonts/SimKBBd_.ttf') format('truetype'); /* IE9 Compat Modes */
	  font-weight: bold;
	  font-style: normal;
	}

	@font-face {
	  font-family: 'SimpleKölnBonn';
	  src: url('../assets/fonts/SimKBBIt.ttf') format('truetype'); 
	  font-weight: bold;
	  font-style: italic;
	}

	@font-face {
	  font-family: 'SimpleKölnBonn';
	  src: url('../assets/fonts/SimKBRg_.ttf') format('truetype'); 
	  font-weight: normal;
	  font-style: normal;
	}


	@font-face {
	  font-family: 'SimpleKölnBonn';
	  src: url('../assets/fonts/SimKBIt_.ttf') format('truetype'); 
	  font-weight: normal;
	  font-style: italic;
	}

	html,body{
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
		background-color: #fbfbfb;
	}

	#app-container::after, #app-container::before, body::after, body::before{
		content: "";
		display: block;
		position: fixed;
		z-index: 5;
		box-sizing: border-box;
	}
	/*
	#app-container::after{
		width: calc(100% - 20px);
		height: 0;
		top: 10px;
		left: 10px;
		border-top: 3px solid #444;
		overflow-x:hidden;
	}

	#app-container::before{
		width: calc(100% - 20px);
		height: 0;
		bottom: 10px;
		left: 10px;
		border-bottom: 3px solid #444;
	}

	body::after{
		width: 0;
		height: calc(100% - 20px);
		top: 10px;
		left: 10px;
		border-left: 3px solid #444;
	}

	body::before{
		width: 0;
		height: calc(100% - 20px);
		top: 10px;
		right: 10px;
		border-right: 3px solid #444;
	}
	*/

	*{
		font-family: 'SimpleKölnBonn', Helvetica, Arial, sans-serif;
		box-sizing: border-box;
		color: #444;
	}

	body{
		overflow-x: hidden;
		width: 100vw;
	}

	.right-link, .left-link{
		display: block;
		text-transform: uppercase;
		font-weight: bold;
		font-size: 12px;
	}

	.right-link{
		float: right;
	}

	@media (max-width: 800px){
		.right-link, .left-link{
			font-size: 10px;
		}
	}
</style>


