<template>
	<nav>
		<img id="top-dots-image" src="../assets/img/dots.png">
		<h3>Work categories</h3>
		<ul>
			<li v-for="icon in icons">
				<a :class="{active: icon.categoryid == activeCategory}" @click="() => {scrollToElement(icon.categoryid)}">
					<img :src="icon.src">
				</a>
			</li>
		</ul>
	</nav>
</template>

<script>
	import { mapState } from 'vuex'

	import scroll from 'scroll'
	import page from 'scroll-doc'

	export default {
		name: 'side-nav',
		data () {
			return {
				msg: "dit is een test"
			};
		},
		methods: {
			scrollToElement(id){
				console.log(document.getElementById(id).offsetTop);
				scroll.top(page(), document.getElementById(id).offsetTop, {duration: 1000}, (error, scrollTop) => {console.log(scrollTop)});
			}
		},
		computed: mapState({
			icons(state){
				let i = 0;
				let arr = [];
				for(let category of state.categories){
					arr.push({
						src: require('../assets/img/icons/'+category.icon),
						categoryid: category.id
					});
					i++;
				}
				return arr;
			},
			highlight(state){
				return {top: "300px"};
			},
			activeCategory: (state) => {
				return state.activeCategory
			},

		})
	}
</script>

<style scoped lang="scss">

	nav{

		position: absolute;
		left: 50%;
		margin-left: calc(-35vw - 100px);
		z-index: 3;
		position: fixed;
		top: 100px;

		ul{
			list-style-type: none;
			margin:0;
			padding:0;

			a{
				display: block;
				padding: 0px 5px;
				width: 10px;
				height: 66px;
				position: relative;
				cursor: pointer;
			}

			a::after{
				content: "";
				display: block;
				height: 32px;
				position: absolute;
				left: 12px;
				top: 25px;
				border: 0.5px solid black;
				z-index: 1;
			}

			a.active::before{
				content: "";
				display: block;
				width: 28px;
				height: 28px;
				background-image: url("../assets/img/dots.png");
				position: relative;
				z-index: 2;
				position: absolute;
				top: -7px;
				left: 0;
			}

			a img{
				width: 16px;
				height: auto;
				background-color: #fff;
				border-radius: 10px;
			}

		}


		h3{
			transform: rotate(90deg) translate(0, -5.5px);
			transform-origin: bottom left;
			margin: 0 0 140px 0;
			text-transform: uppercase;
			font-size: 12px;
			position: relative;
			font-weight: bold;
		}

		/* fades */

		ul::before, ul::after{
			content: "";
			display: block;
			height: 30px;
			position: absolute;
			left: 12px;
			width: 1px;
			z-index: 2;
		}

		ul::before{
			top: 115px;
			background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
			background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
			background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 );
			background-color: black;
		}

		ul::after{
			bottom: 8px;
			background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
			background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
			background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 );
		}

		#top-dots-image{
			position: absolute;
			top: -40px;
		}

		#highlight-dots-image{
			position: absolute;
			top: 148px;
			z-index: 10;
		}
	}

	/*stripes*/





</style>


