<template>
  <div>
    <div id="intro-container">
  		<h1 id="martina-bonetti">Martina Bonetti</h1>
  		<h2  :style="elementStyle"id="intro-text">If I could be anything, <br>then I'd be an astronaut. <br> <br>
  		I settled on <span class="underline">service design</span>, <br>but I still like to push boundaries & work with always different and motivated people.</h2>
  		<img :style="elementStyle" id="drawing" src="../assets/img/drawing.png" alt="Martina Bonetti - Service designer">

  		<img class="frontpage-icon" :style="iconStyle" id="icon-calendar" src="../assets/img/icons/calendar.png" alt="calendar">
  		<img class="frontpage-icon" :style="iconStyle" id="icon-cupcake" src="../assets/img/icons/cupcake.png" alt="cupcake">
  		<img class="frontpage-icon" :style="gestureStyle" id="icon-gesture" src="../assets/img/icons/gesture.png" alt="gesture">
  		<img class="frontpage-icon" :style="iconStyle" id="icon-pizza" src="../assets/img/icons/pizza.png" alt="pizza">
  		<img class="frontpage-icon" :style="iconStyle" id="icon-scotland-yard" src="../assets/img/icons/scotland-yard.png" alt="scotland-yard">
  		<img class="frontpage-icon" :style="iconStyle" id="icon-search" src="../assets/img/icons/search.png" alt="search">
  		<img class="frontpage-icon" :style="rocketStyle" id="icon-rocket" src="../assets/img/icons/rocket.png" alt="rocket">
    </div>
    <div id="bottom-pattern"></div>
  </div>
</template>

<script>

import { mapState } from 'vuex'

export default {
	name: 'intro',
	data () {
		return {
			msg:"test"
		};
	},
	computed: mapState({
		rocketStyle(state){
			if(state.scrollPos > window.innerHeight) return;
			return {transform: `translate(${ (state.scrollPos*0.5) }px, ${state.scrollPos*-0.3}px) rotateZ(${state.scrollPos/10}deg)`};
		},
		gestureStyle(state){
			if(state.scrollPos > window.innerHeight) return;
			return {transform: `translateY(${state.scrollPos*0.2}px) rotateZ(${Math.sin(state.scrollPos/50)*30}deg) `}
		},
		iconStyle(state){
			if(state.scrollPos > window.innerHeight) return;
			return {transform: `translateY(${state.scrollPos*0.2}px) rotateZ(${Math.sin(state.scrollPos/100)*10}deg) `}
		},
		elementStyle(state){
			if(state.scrollPos > window.innerHeight) return;
			return {transform: `translateY(${state.scrollPos*0.5}px) rotateZ(${Math.sin(state.scrollPos/50)*0}deg) `}
		}
	})
}
</script>

<style scoped lang="scss">

  #intro-container{
    width: 100vw;
    height: calc(100vh);
    text-align: center;
    padding-top: 100px;
    background-color: #E3897D;
    position: relative;
    z-index: 20;
    overflow: hidden;
  }

  #bottom-pattern{
    position: relative;
    width: 100vw;
    height: 78px;
    background: url('../assets/img/intro-pattern.png');
    background-size: 32px 78px;
    margin-bottom: 250px;
    z-index: 20;
  }

  #intro-page *{
    color: white;
  }

  #martina-bonetti{
    position: absolute;
    top: 40px;
    left: 50%;
    margin: 0 0 0 -500px;
  }

  #intro-text{
    position: absolute;
    left: calc(50% + 10px);
    top: 25%;
    width: 480px;
    text-align: left;
    font-size: 2em;
    font-weight: normal;
  }

  #drawing{
    position: absolute;
    width: 300px;
    left: calc(50% - 500px);
    top: calc(25% + 20px);
  }


  .underline{
    text-decoration: underline;
  }

  .frontpage-icon{
    position: absolute;
    width: 50px;
  }

  #icon-calendar{
    top: 23%;
    left: 7%;
  }

  #icon-gesture{
    top: 78%;
    left: 14%;
  }

  #icon-cupcake{
    top: 18%;
    left: 78%;
  }

  #icon-pizza{
    top: 82%;
    left: 63%;
  }

  #icon-scotland-yard{
    top: 74%;
    left: 84%;
  }

  #icon-rocket{
    top: 65%;
    left: 42%;
  }

  #icon-search{
    top: 50%;
    left: 90%;
  }

  @media (max-width: 800px) {
    #martina-bonetti{
      left: 20px;
      margin-left: 0;
    }

    #drawing{
      height: 200px;
      width: auto;
      left: 20px;
      top: 100px;
    }

    #intro-text{
      top: 300px;
      left: 20px;
      width: calc(100% - 40px);
      font-size: 1.6em;
    }

    .frontpage-icon{
      width: 25px;
    }
  }
</style>


