<template>

	<div  class="category-item" :id="categorydata.id" :style="styleObject.container">
		<div :style="styleObject.item">
			<router-link :to="'/portfolio/' + categorydata.id">
				<h2 :style="styleObject.h2">{{categorydata.number}}</h2>
				<h1 :style="styleObject.h1" ref="title">{{categorydata.title}}</h1>
				<img :style="styleObject.image" class="main-image" :src="image1" alt="big image">
			</router-link>

			<div :style="styleObject.related" class="related-projects" >
				<h3>Related projects</h3>
				<router-link v-for="relatedItem in categorydata.relatedItems" :to="'/portfolio/' + categorydata.id + '/' + relatedItem.id" :key="relatedItem.id">
					<img :src="relatedImages[relatedItem.id]" alt="small image">
				</router-link>
			</div>
		</div>
	</div>

	<!--
	<div class="portfolio-item" :class="{active: inScroll}" :id="itemdata.id">
		<a href="#">
			<h2>01</h2>
			<h1 ref="title">Public services</h1>
			<img class="main-image" src="../assets/img/portfolio-big.png" alt="big image">
		</a>

		<div class="related-projects" >
			<h3>Related projects</h3>
			<a href="#">
				<img src="../assets/img/portfolio-small-1.png" alt="small image">
			</a>
			<a href="#">
				<img src="../assets/img/portfolio-small-2.png" alt="small image">
			</a>
		</div>
	</div>
	-->
</template>

<script>

	/*
	Individual categories, to be shown in the Index component.
	Should reference an object that contains a portfolio item:
	title, big image, icon for in the left bar, (number?), references to related projects (other objects? not sure how to do this)

	How we can dynamically update styles:
	bind style with :style="object"
	-> object can be computed value
	*/
	import { mapState } from 'vuex'
	import BezierEasing from 'bezier-easing'
	import {store} from '../store'

	var eased = BezierEasing(0,1,1,0);

	export default {
		name: 'category-item',
		props: ["categorydata"],
		data () {
			return {
				image1: "",
				relatedImages: {}
			};
		},
		computed: mapState({
			styleObject(state){
				if(window.innerWidth < 800) return {container: null, item: null, h1: null, h2: null, related: null, image: null};
				let rest = {
					container: null,
					h2: {transform: `translate(150vw,0px)` },
					h1: {transform: `translate(100vw,0px)` },
					related: {transform: `translate(100vw, 0px)`},
					image: {transform: `translate(0px, 100vh)`}
				};
				let relativePosition = (state.scrollPos-this.offsetTop) / window.innerHeight;
				if(relativePosition < -1 || relativePosition > 1) return rest;
				if(relativePosition > -0.5 && relativePosition < 0.5) store.commit("activeCategory", this.categorydata.id)
				let t = eased( (relativePosition + 1) / 2 ) * 2 - 1 ;
				return {
					container: null,
					item: { position: 'fixed', top: '0',height: '100vh', width: '70vw', margin: 'auto', overflow: 'hidden'},
					h1: {transform: `translate(${-t * 100}vw,0px)` },
					h2: {transform: `translate(${-t * 150}vw,0px)` },
					related: {transform: `translate(${-t * 50}vw, 0px)`, opacity: `${((t * 4)+1)}`},
					image: {transform: `translateY(${-t * window.innerHeight}px)`}
				}
			}
		}),
		updated() {
			this.offsetTop = this.$el.offsetTop;
		},
		mounted(){
			this.image1 = require('../assets/img/' + this.categorydata.mainItem.featuredImage);
			if(this.categorydata.relatedItems[0]) this.relatedImages[this.categorydata.relatedItems[0].id] = require('../assets/img/' + this.categorydata.relatedItems[0].featuredImage);
			if(this.categorydata.relatedItems[1]) this.relatedImages[this.categorydata.relatedItems[1].id] = require('../assets/img/' + this.categorydata.relatedItems[1].featuredImage);
		}
	}


</script>

<style lang="scss">
	.category-item{
		position: relative;
		height: 100vh;
		width: 70vw;
		margin: auto;
		overflow: hidden;
		margin: 0 auto 80vh auto;

		h2, h1{
			position: absolute;
			font-size: 3.6em;
			top: 150px;
			z-index: 3;
			width: 60%;
			text-transform: uppercase;
		}

		h1{
			position: absolute;
			top: 220px;
		}

		h2{
			text-stroke: 2px #444;
			-webkit-text-stroke: 2px #444;
			color: transparent;
		}

		.main-image{
			position: absolute;
			right: 0;
			bottom: 20%;
			width: 45%;
			height: auto;
			overflow: visible;
			z-index: 2;
		}


		.related-projects{
			position: absolute;
			left: 0;
			bottom: 20%;
			width: 55%;
			font-size: 0;
			z-index: 1;

			img{
				width: 35%;
				height: auto;
				float: right;
				margin-right: 20px;
			}

			h3{
				font-size: 26px;
				font-weight: lighter;
				display: inline-block;
				width: 70px;
				margin: 0;
			}
		}

		.related-projects, h1, h2, .main-image{
			transform: translate(0px,0px);
			opacity: 1;
			transition: none;

		}
	}

	@media (max-width: 800px){
		.category-item{
			width: 100vw;
			margin-bottom: 0;

			h1,h2{
				font-size: 3em;
				top: 40px;
				left: 40px;
				width: calc(100% - 80px);
			}

			h1{
				top: 100px;
			}

			.main-image{
				width: calc(100% - 80px);
				left: 40px;
			}

			.related-projects{
				bottom: 0;
				width: 100%;
				padding: 0 20px 0 40px;

				img{
					width: 30%;
				}

				h3{
					font-size: 20px;
					width: 60px;
				}
			}

		}

	}


</style>


